import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

export const baseURL : string = 'http://10.164.1.186:2000';

@Injectable()
export class UserAuthenticationService {
    constructor(private http:Http){}
    
    login(email: string, password: string): Promise<any>{
        return this.http.post(baseURL+'/auth/login',{
            email,
            password
        })
        .toPromise()
        .then(this.handleErrors);
    }

    forgetPassword(email: string): Promise<any>{
        return this.http.post(baseURL+'/auth/forgotpassword',{
            email
        })
        .toPromise()
        .then(this.handleErrors);
    }

    resetPassword(code: string, password: string){
        return this.http.post(baseURL+'/auth/resetpassword',{
            code,
            password
        })
        .toPromise()
        .then(this.handleErrors);
    }

    handleErrors(response: any): Promise<any>{
        response = response.json();
        if(response.status !== 'success'){
            return Promise.reject(response.message);
        }
    }

}


