import { Component } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
import { URLSearchParams } from '@angular/http';


import { UserAuthenticationService } from '../services/authentication.service'

@Component({
  selector: 'authentication',
  template: require('./authentication.component.html'),
  styles: [require('./font.icon.css'), require('./authentication.component.style.css')]
})
export class AuthenticationComponent {
  currentView: string = 'signin';
  uiMessages: string;
  paramsSubscription: any;
  loginForm: FormGroup;
  forgotPasswordForm: FormGroup;
  resetPasswordForm: FormGroup;
  resetCode: string;


  constructor(private router: Router, private activatedRoute: ActivatedRoute, private authenticationService: UserAuthenticationService) {}

  ngOnInit() {
    
    this.paramsSubscription = this.activatedRoute.params.subscribe(params => {
      this.uiMessages = "";
      switch (params['action']) {
        case 'signin':
          this.currentView = 'signin';
          break;
        case 'forgotpassword':
          this.currentView = 'forgotpassword';
          break;
        case 'resetpassword':
          this.currentView = 'resetpassword';
          this.resetCode = this.router.routerState.snapshot.root.queryParams['code'];
          break;
        default:
          this.currentView = 'signin';
      }
    });
    this.initialiseForms();
  }

  onLoginClicked(formValue:any){
    this.uiMessages = "Please Wait...";
    this.authenticationService.login(formValue.email,formValue.password)
    .then(response=>{
      this.router.navigateByUrl("/dashboard");
    })
    .catch((error)=>{this.showErrors(error)});
  }

  onForgetPasswordClicked(formValue:any){
    this.uiMessages = "Please Wait..."
    this.authenticationService.forgetPassword(formValue.email)
    .then(response => {
      this.uiMessages = "Email has been sent to your account."
    })
    .catch((error)=>{this.showErrors(error)});
  }

  onResetPasswordClicked(formValue:any){
    this.authenticationService.resetPassword(this.resetCode,formValue.password)
    .then(()=>{
      this.uiMessages = "Your password has been changed successfully."
    })
    .catch((error)=>{this.showErrors(error)});
  }

  initialiseForms(){
    this.loginForm = new FormGroup({
      email: new FormControl(),
      password: new FormControl()
    });

    this.forgotPasswordForm = new FormGroup({
      email: new FormControl()
    });

    this.resetPasswordForm = new FormGroup({
      password: new FormControl()
    });
  }

  showErrors(message: string){
    this.uiMessages = message;
  }


  ngOnDestroy(){
    this.paramsSubscription.unsubscribe();
  }

}



